import React, { Component } from "react";
import { Button, Progress } from "semantic-ui-react";

export default class ProgressExampleIndicating extends Component {
  state = { percent: 33 };

  render() {
    let progress;
    if (this.props.selectedProject.progress === null) {
      progress = 0;
    } else {
      progress = this.props.selectedProject.progress;
    }
    return (
      <div>
        <Progress percent={this.props.selectedProject.progress} indicating />
      </div>
    );
  }
}
